.footer {
    background-color: #32383e;
    background-image: -webkit-gradient(linear, right top, left top, from(#f45c43), to(#eb3449));
    background-image: linear-gradient(to left, #f45c43 0%, #eb3449 100%);
    color           : #ffb5b7;
    width           : 100%;
    height          : 14rem;
}

.footer_container {
    width      : 100%;
    max-width  : 1280px;
    margin     : 0 auto;
    padding-top: 1rem;

    h2 {
        margin    : 0 auto;
        text-align: center;
    }
}

.footer-contacts{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    min-height: 5rem;
    margin-top: 2rem;
}
.footer-link {
    color: #fff;
    font-size: 2rem;
    .MuiSvgIcon-root{
        font-size: 50px;
    }
    &:hover{
        color:#f4beba;
    }
}
.footer-link:last-child {
    .MuiSvgIcon-root{
        font-size: 42px;
    }
}

.footer-social_links{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 15rem;
}
.footer-contacts_info{
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    height: 8rem;
    align-items: flex-start;
    .footer-link {
        display: flex;
        color:#f4beba;
        font-size: 1.7rem;
        .MuiSvgIcon-root{
            font-size: 40px;
            margin-right: 1rem;
        }
        &:hover{
            color:#fff;
        }
    }
}

.footer-copyright{
width: 100%;
font-size: 1.3rem;
text-align: center;
}

@media (max-width: 1000px){
    .footer-contacts_info{
    .footer-link {
        font-size: 1.4rem;
        }
    }
}

@media (max-width: 769px){
    .footer-social_links{
        width: 13rem;
    }
    .footer-contacts_info{
        height: 6rem;
        margin-bottom: 1rem;
    .footer-link {
        font-size: 1.2rem;
        }
    }
    .footer-link {
        font-size: 1rem;
        .MuiSvgIcon-root{
            font-size: 45px;
        }
    }
    .footer-link:last-child {
        .MuiSvgIcon-root{
            font-size: 37px;
        }
    }
    .footer-copyright{
        font-size: 1.1rem;
        }     
}
@media (max-width: 650px){

    .footer-contacts{
        flex-direction: column-reverse;
        margin-top: .2rem;
        width: 80%;
        margin: 0 auto;
        margin-bottom: 1rem;
    }    
    .footer-social_links{
        width: 16rem;
        // flex-direction: column;
        // height: 9rem;
    }
    .footer-link {
        font-size: 1rem;
        .MuiSvgIcon-root{
            font-size: 38px;
        }
    }
    .footer-link:last-child {
        .MuiSvgIcon-root{
            font-size: 31px;
        }
    }
    .footer-contacts_info{
        margin-bottom: 0;
    .footer-link {
        font-size: 1rem;
        .MuiSvgIcon-root{
            font-size: 28px;
            margin-right: .1rem;
        }
        }
    }
    .footer-copyright{
        font-size: .9rem;
        } 
}