@import '../assets/style/colors.scss';

.nav_bar-header{
    width: 100%;
    height: 10rem;
    background-image: url("../assets/images/macbook.jpg");
    background-size: cover;
    background-position: center;
}
.nav_wrapper{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    nav{
        width: 40rem;
        display: flex;
        justify-content: space-around;
        .nav_link{
            color: whitesmoke;
            font-size: 30px;
            font-weight: 500;
            text-transform: lowercase;
            font-family: 'Poppins', sans-serif;
            transition: 1.3s cubic-bezier(0.075, 0.82, 0.165, 1.5);
            &:hover{
                color: $btn_hover-color;
                transform: scale(1.15);
            }
        }
        .nav_link-active{
            color: #fd4b46;
            border-bottom: 3px solid $btn_hover-color;
        }
    }
}