@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap);
*,*::after,*::before{margin:0;padding:0;box-sizing:border-box}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}h2{font-family:'Poppins', sans-serif}a{text-decoration:none}li{list-style:none}.back-video{position:absolute;left:0;top:0;width:100%;height:100%;object-fit:cover;opacity:0.8}.text-red{color:brown}.text-red:hover{background-color:aqua}.loading-ring{display:inline-block;width:80px;height:80px}.loading-ring:after{content:" ";display:block;width:64px;height:64px;margin:8px;border-radius:50%;border:6px solid rgba(224,96,96,0.883);border-color:rgba(224,96,96,0.883) transparent rgba(224,96,96,0.883) transparent;-webkit-animation:loading-ring 1.2s linear infinite;animation:loading-ring 1.2s linear infinite}@-webkit-keyframes loading-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes loading-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.loading-ripple{display:inline-block;position:relative;width:80px;height:80px}.loading-ripple div{position:absolute;border:4px solid #cef;opacity:1;border-radius:50%;-webkit-animation:loading-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;animation:loading-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite}.loading-ripple div:nth-child(2){-webkit-animation-delay:-0.5s;animation-delay:-0.5s}@-webkit-keyframes loading-ripple{0%{top:36px;left:36px;width:0;height:0;opacity:1}100%{top:0px;left:0px;width:72px;height:72px;opacity:0}}@keyframes loading-ripple{0%{top:36px;left:36px;width:0;height:0;opacity:1}100%{top:0px;left:0px;width:72px;height:72px;opacity:0}}

.about{background-image:url(/personal/static/media/back_from_apple.1bdcfd4f.jpg);background-repeat:no-repeat;background-size:100% 100%;min-height:calc(100vh - 10rem)}.about_img{width:400px;border-radius:10px;margin:3rem}.about_container{display:flex;justify-content:space-evenly;align-items:center;width:100%;max-width:1280px;min-height:calc(100vh - 24rem);margin:0 auto;padding:2rem;padding-top:4rem;color:ivory;font-family:"Proxima Nova", "Helvetica Neue", Helvetica, Arial, sans-serif}.about_discribtion{display:flex;flex-direction:column;align-items:flex-start}.about_discribtion h1{margin-bottom:1rem}.about_discribtion-item{width:70%;font-size:25px;line-height:26px;font-weight:300;margin:0 0 16px;font-style:italic}

.footer{background-color:#32383e;background-image:linear-gradient(to left, #f45c43 0%, #eb3449 100%);color:#ffb5b7;width:100%;height:14rem}.footer_container{width:100%;max-width:1280px;margin:0 auto;padding-top:1rem}.footer_container h2{margin:0 auto;text-align:center}.footer-contacts{display:flex;justify-content:space-evenly;align-items:center;min-height:5rem;margin-top:2rem}.footer-link{color:#fff;font-size:2rem}.footer-link .MuiSvgIcon-root{font-size:50px}.footer-link:hover{color:#f4beba}.footer-link:last-child .MuiSvgIcon-root{font-size:42px}.footer-social_links{display:flex;justify-content:space-between;align-items:center;width:15rem}.footer-contacts_info{display:flex;justify-content:space-evenly;flex-direction:column;height:8rem;align-items:flex-start}.footer-contacts_info .footer-link{display:flex;color:#f4beba;font-size:1.7rem}.footer-contacts_info .footer-link .MuiSvgIcon-root{font-size:40px;margin-right:1rem}.footer-contacts_info .footer-link:hover{color:#fff}.footer-copyright{width:100%;font-size:1.3rem;text-align:center}@media (max-width: 1000px){.footer-contacts_info .footer-link{font-size:1.4rem}}@media (max-width: 769px){.footer-social_links{width:13rem}.footer-contacts_info{height:6rem;margin-bottom:1rem}.footer-contacts_info .footer-link{font-size:1.2rem}.footer-link{font-size:1rem}.footer-link .MuiSvgIcon-root{font-size:45px}.footer-link:last-child .MuiSvgIcon-root{font-size:37px}.footer-copyright{font-size:1.1rem}}@media (max-width: 650px){.footer-contacts{flex-direction:column-reverse;margin-top:.2rem;width:80%;margin:0 auto;margin-bottom:1rem}.footer-social_links{width:16rem}.footer-link{font-size:1rem}.footer-link .MuiSvgIcon-root{font-size:38px}.footer-link:last-child .MuiSvgIcon-root{font-size:31px}.footer-contacts_info{margin-bottom:0}.footer-contacts_info .footer-link{font-size:1rem}.footer-contacts_info .footer-link .MuiSvgIcon-root{font-size:28px;margin-right:.1rem}.footer-copyright{font-size:.9rem}}

.contacts{background:#292e33;background-image:linear-gradient(331deg, #272c31 0%, #32383e 100%);color:#6e767c;min-height:calc(100vh - 10rem)}.contacts_body{min-height:calc(100vh - 24rem);max-width:1280px;margin:0 auto;display:flex;flex-direction:column;padding:2rem;font-size:1.5rem}.contacts_body h2{margin:0 auto;text-transform:uppercase;border-bottom:4px solid #fd4b46;font-size:2rem}.contacts_body p{margin:1.5rem 0}.contacts_body-title{font-size:1.6rem;color:#fd4b46;margin:1.5rem 0}.contact_form-wraper{display:flex;flex-direction:column;align-items:center;border-top:1.5px solid black;padding-top:2rem}#contact_form{display:flex;flex-direction:column;align-items:center;width:90%}#contact_form .contact_form-top{display:flex;justify-content:space-between;width:100%;margin-bottom:2rem}#contact_form .contact_form-top input{width:47%;height:3rem;padding-left:1rem;font-size:1rem}.contact_form-message{width:100%;height:10rem;padding:1rem;font-size:1.3rem}#submit{color:#fff;background:#fd4b46;border:1px solid #fd4b46;width:100px;height:34px;margin-bottom:0.5rem;margin-top:2rem;display:flex;justify-content:space-around;align-items:center;font-size:15px;font-family:"Halis Rounded", "Helvetica Neue", Helvetica, Arial, sans-serif;font-weight:700;letter-spacing:0.2rem;transition:all .2s ease-in-out;text-transform:uppercase;border-radius:25px}#submit:hover{background:#fc1a14;border-color:#f40903;color:#fff}

.home-wrapper{background-image:linear-gradient(rgba(255,255,255,0.5), rgba(255,255,255,0.5)),url(/personal/static/media/imac2.33070c7f.jpg);background-repeat:no-repeat;background-size:cover;min-height:calc(100vh - 10rem);align-items:center;display:flex}.home_h1{color:#545454;min-height:calc(100vh - 10rem)}.home_container{display:flex;margin:0 auto;max-width:1280px;justify-content:space-evenly;min-height:calc(100vh - 10rem);width:100%;padding-top:10rem;background-image:url(/personal/static/media/imac2.33070c7f.jpg);background-repeat:no-repeat;background-size:cover;z-index:1}.home_container .home_img{left:10rem;top:20rem;width:20rem;border-radius:10px}.home_container .home_greeting{display:flex;flex-direction:column;align-items:center;width:40%}.home_container .home_greeting .home_title{width:90%;margin-top:1rem;text-align:center;margin-bottom:0;line-height:1.5;font-family:"Halis Rounded", "Helvetica Neue", Helvetica, Arial, sans-serif;color:#2e2e2e;font-size:2.5rem;font-weight:700}.home_container .home_greeting .home_subtitle{text-align:center;color:dimgray;font-size:21px;line-height:26px;text-transform:none;font-weight:300;margin:0 0 16px;font-style:italic;font-family:"Proxima Nova", "Helvetica Neue", Helvetica, Arial, sans-serif;border-bottom:2px solid #2e2e2e}.home_container .home_greeting .home_link{color:#fff;background:#fd4b46;border:1px solid #fd4b46;width:115px;height:29px;margin-bottom:0.5rem;display:flex;align-items:center;font-size:13px;font-family:"Halis Rounded", "Helvetica Neue", Helvetica, Arial, sans-serif;font-weight:700;transition:all .2s ease-in-out;text-transform:uppercase;border-radius:25px;padding:5px 16px 5px}.home_container .home_greeting .home_link:hover{background:#fc1a14;border-color:#f40903;color:#fff}

.nav_bar-header{width:100%;height:10rem;background-image:url(/personal/static/media/macbook.62832eee.jpg);background-size:cover;background-position:center}.nav_wrapper{width:100%;height:100%;display:flex;align-items:center;justify-content:flex-end}.nav_wrapper nav{width:40rem;display:flex;justify-content:space-around}.nav_wrapper nav .nav_link{color:whitesmoke;font-size:30px;font-weight:500;text-transform:lowercase;font-family:'Poppins', sans-serif;transition:1.3s cubic-bezier(0.075, 0.82, 0.165, 1.5)}.nav_wrapper nav .nav_link:hover{color:#fc1a14;transform:scale(1.15)}.nav_wrapper nav .nav_link-active{color:#fd4b46;border-bottom:3px solid #fc1a14}

.projects{display:flex;flex-direction:column;align-items:center;margin:0 auto;width:100%;max-width:1400px}.projects h2{margin:1.5rem auto;text-transform:uppercase;color:#2e2e2e;font-size:2rem;border-bottom:2px solid #fd4b46;font-family:"Halis Rounded", "Helvetica Neue", Helvetica, Arial, sans-serif;font-weight:700}.projects h3{margin:1.5rem auto;color:#545454;font-size:1.6rem;font-family:"Halis Rounded", "Helvetica Neue", Helvetica, Arial, sans-serif;font-weight:300}.projects .projects_list{display:flex;flex-direction:column;align-items:flex-start}.projects .projects_list .projects_list-item{display:flex;width:100%;margin:0;padding:1rem;background-image:linear-gradient(to right, white 40%, rgba(250,212,216,0.541176) 80%, white 100%)}.projects .projects_list .projects_list-item .project{display:flex;width:100%}.projects .projects_list .projects_list-item .project-image_container{display:flex;align-items:center;width:50%;margin-right:1rem;margin-left:1rem;align-items:center}.projects .projects_list .projects_list-item img{width:100%}.projects .projects_list .projects_list-item:nth-child(even){background-image:linear-gradient(to left, white 40%, rgba(250,212,216,0.541176) 80%, white 100%)}.projects .projects_list .projects_list-item:nth-child(even) .project{flex-direction:row-reverse}.project-about{display:flex;flex-direction:column;justify-content:space-between;align-items:center;width:47%}.project-about .project_about-top{display:flex;flex-direction:column}.project-about .project_about-top .project_about-title{width:90%;margin-top:1rem;text-transform:uppercase;text-align:center;margin-bottom:0;line-height:1.5;font-family:"Halis Rounded", "Helvetica Neue", Helvetica, Arial, sans-serif;color:#2e2e2e;font-size:1.5rem;font-weight:700}.project-about .project_about-top .project_about-subtitle{text-align:center;font-weight:400;font-size:1rem;letter-spacing:.15em;text-transform:uppercase;font-family:"Proxima Nova", "Helvetica Neue", Helvetica, Arial, sans-serif;color:dimgray;margin-bottom:1rem;line-height:1.5}.project-about .project-link{color:#fff;background:#fd4b46;border:1px solid #fd4b46;width:124px;height:34px;margin-bottom:0.5rem;display:flex;align-items:center;font-size:13px;font-family:"Halis Rounded", "Helvetica Neue", Helvetica, Arial, sans-serif;font-weight:700;transition:all .2s ease-in-out;text-transform:uppercase;border-radius:25px;padding:5px 16px 5px}.project-about .project-link:hover{background:#fc1a14;border-color:#f40903;color:#fff}.project-about .project-technologies{display:flex;align-items:center;width:95%}.project-about .project-technologies .project_stack-list{display:flex;justify-content:flex-start;flex-wrap:wrap}.project-about .project-technologies .project_stack-list .project_stack{font-size:0.8rem;margin:0.3rem;border:1px solid dimgray;border-radius:5px;padding:3px 3px 5px 3px}

